import { cards } from './cards';
import './css/page.css';
import { startPage } from './script';
export function renderLevelPage(box, cardsCount) {
    var openHtml = '';
    var firstCard;
    var secondCard;
    var myTime = '';
    for (var i = 0; i <= cardsCount; i++) {
        var random = cards[Math.floor(Math.random() * cards.length)];
        openHtml += "\n        <div data-index=\"".concat(i, "\">\n        <img data-value=\"").concat(random, "\"class=\"card\" src='./static/asset/jpg/").concat(random, ".jpg' alt='").concat(random, "'/>\n        </div>");
        cards.splice(cards.indexOf(random), 1);
    }
    var headHtml = "\n    <header class=\"head\">\n    <div class=\"timetable\">\n        <div class=\"minutes\">\n            <div class=\"min\">\n                <p>min</p>\n            </div>\n            <div class=\"min\">\n                <p>sek</p>\n            </div>\n        </div>\n        <div class=\"time\">\n            <time></time>\n        </div>\n    </div>\n    <div class=\"startAgain\">\n        <button class=\"buttonstart\">\u041D\u0430\u0447\u0430\u0442\u044C \u0437\u0430\u043D\u043E\u0432\u043E</button>\n    </div>\n</header>";
    var open = "<div class=\"tops\">".concat(headHtml, "\n <div class=\"cards\">").concat(openHtml, "</div> </div>");
    box.innerHTML = open;
    setTimeout(function () {
        var card = document.querySelectorAll('.card');
        card.forEach(function (car) {
            car.setAttribute('src', './static/asset/jpg/рубашка.jpg');
        });
    }, 5000);
    var time = 0;
    var timer = setInterval(function () {
        time++;
        var minutes = Math.floor(time / 60)
            .toString()
            .padStart(2, '0');
        var seconds = (time % 60).toString().padStart(2, '0');
        var gameTimer = document.querySelector('.time');
        gameTimer.textContent = "".concat(minutes, ".").concat(seconds);
        myTime = gameTimer.innerHTML;
        var buttonStart = document.querySelector('.buttonstart');
        buttonStart.addEventListener('click', function () {
            time = 0;
            gameTimer.textContent = '00.00';
            clearInterval(timer);
        });
    }, 1000);
    var card = Array.from(document.querySelectorAll('.card'));
    card.forEach(function (car) {
        car.addEventListener('click', function () {
            car.setAttribute('src', "./static/asset/jpg/".concat(car.dataset.value, ".jpg"));
            if (!firstCard) {
                firstCard = car.dataset.value;
            }
            else {
                secondCard = car.dataset.value;
                if (firstCard !== secondCard) {
                    var looser = "<div class=\"container-end\">\n                    <div class=\"text\">\n                    <div><img class=\"image\"src=\"./static/asset/jpg/looser.png\" alt=\"looser\"></div>\n                    <h1 class=\"title-end\">\u0412\u044B \u043F\u0440\u043E\u0438\u0433\u0440\u0430\u043B\u0438!</h1>\n                    </div>\n                    <div class=\"text-end\">\u0417\u0430\u0442\u0440\u0430\u0447\u0435\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F:</div>\n                    <div class=\"time-end\">".concat(myTime, "</div>\n                    <div class=\"again\">\n                    <button class=\"start-again\">\u0418\u0433\u0440\u0430\u0442\u044C \u0441\u043D\u043E\u0432\u0430</button>\n                    </div>\n                    </div>");
                    box.innerHTML = looser;
                    var buttonAgain = document.querySelector('.start-again');
                    buttonAgain.addEventListener('click', function () {
                        startPage();
                    });
                }
                else {
                    var winner = "<div class=\"container-end\">\n                    <div class=\"text\">\n                    <div><img class=\"image\"src=\"./static/asset/jpg/winner.png\" alt=\"winner\"></div>\n                    <h1 class=\"title-end\">\u0412\u044B \u043F\u0440\u043E\u0438\u0433\u0440\u0430\u043B\u0438!</h1>\n                    </div>\n                    <div class=\"text-end\">\u0417\u0430\u0442\u0440\u0430\u0447\u0435\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F:</div>\n                    <div class=\"time-end\">".concat(myTime, "</div>\n                    <div class=\"again\">\n                    <button class=\"start-again\">\u0418\u0433\u0440\u0430\u0442\u044C \u0441\u043D\u043E\u0432\u0430</button>\n                    </div>\n                    </div>");
                    box.innerHTML = winner;
                    var buttonAgain = document.querySelector('.start-again');
                    buttonAgain.addEventListener('click', function () {
                        startPage();
                    });
                }
            }
        });
    });
}
